  font-family: 'Muli', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 13px;
  box-shadow: none;
  height: 40px
}

#search-box-bar #searchSubmit,
#search-box-bar #searchSubmit-mobile {
